    margin: 0px;
    padding: 0px;
}

:root {
    --primary-colors: rgb(83, 82, 82);
    --gray: #b6b5b5;
}

body {
    background: rgb(233, 232, 232);
    font-family: 'Lato', sans-serif;
}

.container {
    max-width: 700px;
    min-height: 200px;
    background: white;
    border: 1px solid rgba(0, 0, 0, 0.178);
    margin: 50px auto;
    display: flex;
    flex-direction: column;
}


.container .header {
    padding: 10px 20px;
    height: 50px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.136);
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.container .header h1 {
    font-size: 19px;
    color: var(--primary-colors);
}

.container .header i {
    font-size: 20px;
    cursor: pointer;
}

.container .form-container {
    flex-basis: 100%;
    width: 100%;
    height: 60px;
    display: flex;
    border-bottom: 1px solid rgba(0, 0, 0, 0.149);
}

.container .form-container form {
    margin: 5px 20px;
    width: 100%;
}

.container .form-container input {
    outline: none;
    border: none;
    height: 50px;
    width: 100%;
    font-style: italic;
}

.container ul {
    list-style: none;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex-basis: 100%;
}

.container ul li {
    border-bottom: 1px solid rgba(0, 0, 0, 0.201);
}


.container ul li .info {
    padding: 30px 10px;
    justify-content: space-between;
    gap: 10px;
    display: flex;
}

.container ul li .info .todo-content {
    display: flex;
    gap: 10px;
}

.container ul li .info .todo-content input {
    cursor: pointer;
}

.container ul li .info .more i {
    cursor: yellow;
    font-size: 20px;
    cursor: pointer;
}

.container ul li.active .info {
    display: none;
    background-color: yellow;
}


.container ul li .actions {
    display: none;
}

.container ul li.active .actions {
    display: flex;
    gap: 40px;
}

.actions {
    background-color: rgba(255, 166, 0, 0.105);
    padding: 30px 10px;
}

.actions form {
    flex-basis: 100%;
}

.actions form input {
    width: 100%;
    font-size: 20px;
    background: transparent;
    outline: none;
    border: none;
}

.actions .delete-btn i {
    margin-right: 10px;
    font-size: 25px;
    cursor: pointer;
}

.clear-btn-container button {
    margin-top: 40px;
    padding: 20px;
    width: 100%;
    cursor: pointer;
    outline: none;
    border: none;
}

.clear-btn-container button:active {
    background-color: var(--gray);
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,iCAAiC;IACjC,eAAe;AACnB;;AAEA;IACI,8BAA8B;IAC9B,+BAA+B;AACnC;;AAEA;IACI,gBAAgB;IAChB,iBAAiB;IACjB,iBAAiB;IACjB,sCAAsC;IACtC,iBAAiB;IACjB,aAAa;IACb,sBAAsB;AAC1B;;;AAGA;IACI,kBAAkB;IAClB,YAAY;IACZ,6CAA6C;IAC7C,aAAa;IACb,mBAAmB;IACnB,8BAA8B;AAClC;;AAEA;IACI,eAAe;IACf,4BAA4B;AAChC;;AAEA;IACI,eAAe;IACf,eAAe;AACnB;;AAEA;IACI,gBAAgB;IAChB,WAAW;IACX,YAAY;IACZ,aAAa;IACb,6CAA6C;AACjD;;AAEA;IACI,gBAAgB;IAChB,WAAW;AACf;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,YAAY;IACZ,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;IAChB,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,gBAAgB;AACpB;;AAEA;IACI,6CAA6C;AACjD;;;AAGA;IACI,kBAAkB;IAClB,8BAA8B;IAC9B,SAAS;IACT,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,cAAc;IACd,eAAe;IACf,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,wBAAwB;AAC5B;;;AAGA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,0CAA0C;IAC1C,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,WAAW;IACX,eAAe;IACf,uBAAuB;IACvB,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,eAAe;AACnB;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,WAAW;IACX,eAAe;IACf,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,6BAA6B;AACjC","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap');\n\n* {\n    margin: 0px;\n    padding: 0px;\n}\n\n:root {\n    --primary-colors: rgb(83, 82, 82);\n    --gray: #b6b5b5;\n}\n\nbody {\n    background: rgb(233, 232, 232);\n    font-family: 'Lato', sans-serif;\n}\n\n.container {\n    max-width: 700px;\n    min-height: 200px;\n    background: white;\n    border: 1px solid rgba(0, 0, 0, 0.178);\n    margin: 50px auto;\n    display: flex;\n    flex-direction: column;\n}\n\n\n.container .header {\n    padding: 10px 20px;\n    height: 50px;\n    border-bottom: 1px solid rgba(0, 0, 0, 0.136);\n    display: flex;\n    align-items: center;\n    justify-content: space-between;